.topbar{
    color: #4895ef;
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}

.topbar-Infor{
    display: flex;
    padding-top: 10px;
    padding-right: 15px;
    align-items: center;

}

.topIcons{
    color: #159be4;
}

@media screen and (max-width: 469px) {
    .topbar{
        width: 100%;
    }
    .topbar-Infor svg{
        font-size: 1.1rem;
    }
    .topBar-parag{
        font-size: 7px;
    }
  }
.products__container{
    display: flex;
}

.products__row1{
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    max-width: 73%;
    min-height: 600px;
    overflow-y: scroll;
}

.products__items{
    display: flex;
    max-width: 100%;
    flex-wrap: wrap;
}

.products__row2{
    border-left: 1px solid #303030;
    width: 25%;
}

.product__divider{
    background-color: #303030;
    margin-bottom: 20px;
}


/* productInfor styling */

.productInfor{
    padding: 15px;
    align-content: center;
}

.productInfor h2{
    margin-bottom: 15px;
}

.productInfor img{
    width: 270px;
    height: 300px;
}


@media screen and (max-width: 811px) {
    .products__row2{
        display: none;
    }
    .products__row1{
        width: 100%;
    }
}

.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: flex-end; */
    margin: 10px;
    padding: 20px;
    width: 15%;
    height: 170px;
    min-width: 100px;

    background-color: white;
    /* z-index: 1; */
}

.product:hover{
    box-shadow: -2px 3px 9px -1px rgba(0,0,0,0.75);
    -webkit-box-shadow: -2px 3px 9px -1px rgba(0,0,0,0.75);
    -moz-box-shadow: -2px 3px 9px -1px rgba(0,0,0,0.75);
    border-radius: 10px;
}

.product > img{
    height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product__info{
    height: 30px;
    margin-bottom: 15px;
}

.product__title{
    font-size: 13px;
}

.product__price{
    margin-top: 5px;
}

.product > button{
    background-color: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

@media screen and (max-width: 469px) {}
.footer{
    /* position: absolute;
    bottom: 0px; */
    background-color: #252525;
    display: flex;
    justify-content: space-around;
